
.header {
  margin: auto;
  text-align: center;
  background-color: black;
  color: white;
  height: 85px;
  padding-top: 25px;
}
body {
  background: #f9f8fd;
  font-family: "Lexend Exa", sans-serif;
}
nav {
  position: relative;
  top: 15px;
  left: 50%;
  transform: translateX(-50%);
  display: inline-block;
  border: 2px solid #ebecf1;
  border-radius: 30px;
  animation: slide-in 1s ease-out;
}
ul {
  position: relative;
  display: flex;
  flex: 1 1 auto;
  margin: 0;
  padding: 0 30px;
  list-style-type: none;
}
ul li:not(:last-child) {
  margin-right: 40px;
}
ul li {
  border: 2px solid transparent;
  border-radius: 5px;
  padding: 10px;
  transition: background 0.2s;
}
ul li a {
  color: black;
  text-decoration: none;
  text-transform: uppercase;
  transition: color 0.2s;
}

ul li:hover {
  background: gray;
  border: 2px solid gray;
  border-right: 2px solid gray;
  border-bottom: 2px solid gray;
  color: #f9f8fd;
}

ul li:hover ul li a {
  color: #f9f8fd;
}
@keyframes slide-in {
  0% {
    top: -50px;
  }
  40% {
    top: 20px;
  }
  70% {
    top: 10px;
  }
  100% {
    top: 15px;
  }
}
