.resultSection{width:50%;margin:auto;padding:50px}.about{position:fixed;z-index:10;bottom:10px;right:10px;width:40px;height:40px;display:flex;justify-content:flex-end;align-items:flex-end;transition:all 0.2s ease}.about .bg_links{width:40px;height:40px;border-radius:100%;display:flex;justify-content:center;align-items:center;background-color:rgba(0,0,0,0.2);border-radius:100%;-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);position:absolute}.about .logo{width:40px;height:40px;z-index:9;background-image:url(https://rafaelavlucas.github.io/assets/codepen/logo_white.svg);background-size:50%;background-repeat:no-repeat;background-position:10px 7px;opacity:0.9;transition:all 1s 0.2s ease;bottom:0;right:0}.about .social{opacity:0;right:0;bottom:0}.about .social .icon{width:100%;height:100%;background-size:20px;background-repeat:no-repeat;background-position:center;background-color:transparent;display:flex;transition:all 0.2s ease, background-color 0.4s ease;opacity:0;border-radius:100%}.about .social.portfolio{transition:all 0.8s ease}.about .social.portfolio .icon{background-image:url(https://rafaelavlucas.github.io/assets/codepen/link.svg)}.about .social.dribbble{transition:all 0.3s ease}.about .social.dribbble .icon{background-image:url(https://rafaelavlucas.github.io/assets/codepen/dribbble.svg)}.about .social.linkedin{transition:all 0.8s ease}.about .social.linkedin .icon{background-image:url(https://rafaelavlucas.github.io/assets/codepen/linkedin.svg)}.about:hover{width:105px;height:105px;transition:all 0.6s cubic-bezier(0.64, 0.01, 0.07, 1.65)}.about:hover .logo{opacity:1;transition:all 0.6s ease}.about:hover .social{opacity:1}.about:hover .social .icon{opacity:0.9}.about:hover .social:hover{background-size:28px}.about:hover .social:hover .icon{background-size:65%;opacity:1}.about:hover .social.portfolio{right:0;bottom:calc(100% - 40px);transition:all 0.3s 0s cubic-bezier(0.64, 0.01, 0.07, 1.65)}.about:hover .social.portfolio .icon:hover{background-color:#698fb7}.about:hover .social.dribbble{bottom:45%;right:45%;transition:all 0.3s 0.15s cubic-bezier(0.64, 0.01, 0.07, 1.65)}.about:hover .social.dribbble .icon:hover{background-color:#ea4c89}.about:hover .social.linkedin{bottom:0;right:calc(100% - 40px);transition:all 0.3s 0.25s cubic-bezier(0.64, 0.01, 0.07, 1.65)}.about:hover .social.linkedin .icon:hover{background-color:#0077b5}body{margin:0}.content{width:100%;height:100vh;background-color:#171f30;display:flex;justify-content:center;align-items:center}.content .loading{width:80px;height:50px;position:relative}.content .loading p{top:0;padding:0;margin:0;color:#5389a6;font-family:"Oxygen",sans-serif;-webkit-animation:text 3.5s ease both infinite;animation:text 3.5s ease both infinite;font-size:12px;letter-spacing:1px}@-webkit-keyframes text{0%{letter-spacing:1px;transform:translateX(0px)}40%{letter-spacing:2px;transform:translateX(26px)}80%{letter-spacing:1px;transform:translateX(32px)}90%{letter-spacing:2px;transform:translateX(0px)}100%{letter-spacing:1px;transform:translateX(0px)}}@keyframes text{0%{letter-spacing:1px;transform:translateX(0px)}40%{letter-spacing:2px;transform:translateX(26px)}80%{letter-spacing:1px;transform:translateX(32px)}90%{letter-spacing:2px;transform:translateX(0px)}100%{letter-spacing:1px;transform:translateX(0px)}}.content .loading span{background-color:#5389a6;border-radius:50px;display:block;height:16px;width:16px;bottom:0;position:absolute;transform:translateX(64px);-webkit-animation:loading 3.5s ease both infinite;animation:loading 3.5s ease both infinite}.content .loading span:before{position:absolute;content:"";width:100%;height:100%;background-color:#a6dcee;border-radius:inherit;-webkit-animation:loading2 3.5s ease both infinite;animation:loading2 3.5s ease both infinite}@-webkit-keyframes loading{0%{width:16px;transform:translateX(0px)}40%{width:100%;transform:translateX(0px)}80%{width:16px;transform:translateX(64px)}90%{width:100%;transform:translateX(0px)}100%{width:16px;transform:translateX(0px)}}@keyframes loading{0%{width:16px;transform:translateX(0px)}40%{width:100%;transform:translateX(0px)}80%{width:16px;transform:translateX(64px)}90%{width:100%;transform:translateX(0px)}100%{width:16px;transform:translateX(0px)}}@-webkit-keyframes loading2{0%{transform:translateX(0px);width:16px}40%{transform:translateX(0%);width:80%}80%{width:100%;transform:translateX(0px)}90%{width:80%;transform:translateX(15px)}100%{transform:translateX(0px);width:16px}}@keyframes loading2{0%{transform:translateX(0px);width:16px}40%{transform:translateX(0%);width:80%}80%{width:100%;transform:translateX(0px)}90%{width:80%;transform:translateX(15px)}100%{transform:translateX(0px);width:16px}}

